import { _storage, Storage, StateValidator } from './models/storage.js';

/**
 * La fonction `createStorage` cre un objet proxy pour grer le stockage avec des mthodes d'accs et de mise  jour des donnes.
 * @template RECORD - Le type de l'enregistrement de stockage.
 * @param {Partial<RECORD>} [records={}] - Un objet partiel de type `RECORD`, contenant des donnes initiales pour le stockage. 
 * @param {StateValidator} [stateValidator] - Une fonction de validation optionnelle appele lors de la modification d'un tat.
 * @returns {Storage<RECORD>} Un objet Proxy qui entoure l'objet de stockage cr en appelant `_storage.init` avec les enregistrements fournis.
 * @example
 * ```typescript
 * const initialData = { counter: 0, name: "Alice" };
 * const validator: StateValidator<number> = (key, previousValue, newValue) => newValue >= 0;
 * const storage = createStorage(initialData, validator);
 * 
 * let [ counter , setCounter ] = storage.get('counter');
 * setCounter( 10 ); // met  jour la valeur si la validation russit
 * console.log(+counter); // affiche 10
 * ```
 */
export function createStorage<RECORD extends Record<string, any>>(records: Partial<RECORD> = {}, stateValidator?: StateValidator): Storage<RECORD> {

  // Cration d'un proxy contenant le stockage
  return new Proxy(_storage.init<RECORD>(records as RECORD, stateValidator), {

    /**
     * Intercepte les oprations d'accs aux proprits du stockage.
     * @param {Storage<RECORD>} target - L'objet cible auquel les oprations sont appliques.
     * @param {string} key - La cl de l'tat ou de la mthode accde.
     * @param {any} receiver - L'objet Proxy qui intercepte l'opration.
     * @returns {any} La mthode ou la valeur de l'tat correspondant  la cl.
     * @example
     * ```typescript
     * const storage = createStorage({ count: 0 });
     * console.log(storage.count[0].value); // affiche 0
     * storage.count ; // met  jour la valeur de count  5
     * ```
    */
    get(target, key: string & any, receiver) {
      // Retourne les mthodes du stockage
      if (target[key]) {
        if (typeof target[key] == "function") return (target[key] as any).bind(target);
        // Retourne les variables du stockage
        else return target[key];
      }
      // Si la cl n'est pas dans le stockage
      // Vrification de la prsence de la cl dans le map du stockage
      // Si prsence de celle-ci, elle est retourne
      else if (target.has(key)) return target.get(key);
      // Sinon la cl n'existe pas et retourne undefined
      else return undefined;
    },

    /**
     * Intercepte les oprations de mise  jour des proprits du stockage.
     * @param {Storage<RECORD>} target - L'objet cible auquel les oprations sont appliques.
     * @param {string} key - La cl de l'tat  mettre  jour.
     * @param {any} newValue - La nouvelle valeur  assigner  l'tat.
     * @param {any} receiver - L'objet Proxy qui intercepte l'opration.
     * @returns {boolean} Un indicateur de succs de la mise  jour.
     * @example
     * ```typescript
     * const storage = createStorage({ count: 0 });
     * storage.count ; // met  jour la valeur de count  5
     * console.log(storage.count[0].value); // affiche 5
     * ```
    */
    set(target, key: string & any, newValue, receiver) {
      if (target.has(key)) {
        // Mise  jour du state contenant la valeur
        target.get(key)[1](newValue);
        return true;
      }
      // Sinon return undefined
      else return undefined;
    }
  }) as any;

}
