import { State, createState } from '@lithium-framework/state';

/**
 * Reprsente la fonction appele lors du changement d'un tat.
 * Cette fonction permet de valider la nouvelle valeur avant de l'appliquer  l'tat.
 * La nouvelle valeur est stocke dans le state correspondant en fonction du succs ou de l'chec de l'opration.
 *
 * @template T - Le type de la valeur de l'tat.
 * @param {string} key - La cl de l'tat en cours de modification.
 * @param {T} previousValue - La valeur prcdente de l'tat avant la modification.
 * @param {T} newValue - La nouvelle valeur propose pour l'tat.
 * @returns {Promise<any> | boolean} - Une promesse ou une valeur boolenne indiquant le succs ou l'chec de la modification.
 * @example
 * ```typescript
 * const validator: StateValidator<number> = (key, previousValue, newValue) => {
 *   if (newValue >= 0) return true;
 *   return false;
 * };
 * ```
 */
export type StateValidator<T = any> = (key: string, previousValue: T, newValue: T) => Promise<any> | boolean;

/** 
 * Type reprsentant les cls des valeurs d'un enregistrement.
 * @template RECORD - Le type de l'enregistrement.
 */
export type StorageKeys<RECORD> = keyof RECORD;

/** 
 * Type reprsentant les mutateurs des valeurs d'un enregistrement.
 * @template RECORD - Le type de l'enregistrement.
 */
export type StorageValues<RECORD> = State<RECORD[StorageKeys<RECORD>]>["mutator"];

/** 
 * La classe `_storage` tend `Map` pour stocker les valeurs d'tat et fournit des mthodes
 * d'initialisation et d'accs aux mutateurs par cl.
 * @template RECORD - Le type de l'enregistrement stock.
 */
export class _storage<RECORD extends Record<string, any>> extends Map<StorageKeys<RECORD>, StorageValues<RECORD>> {

  private _validator: StateValidator = null;

  /**
   * Initialise une nouvelle instance de la classe `_storage` avec des enregistrements initiaux facultatifs et un validateur optionnel.
   * @param {RECORD} [records] - Un objet contenant des paires cl-valeur reprsentant des enregistrements de donnes.
   * @param {StateValidator} [validator] - Une fonction de validation optionnelle appele lors de la modification d'un tat.
   * @returns {Storage<RECORD>} Une nouvelle instance de la classe `_storage` initialise avec les enregistrements et le validateur fournis.
   * @example
   * ```typescript
   * const records = { count: 0, name: "John" };
   * const storage = _storage.init(records);
   * ```
   */
  static init<RECORD extends Record<string, any>>(records?: RECORD, validator?: StateValidator): Storage<RECORD> {
    return new _storage<RECORD>(records, validator) as Storage<RECORD>;
  }

  /**
   * Constructeur qui initialise la classe `_storage` avec des enregistrements optionnels et un validateur.
   * @param {RECORD} [records] - Un objet contenant des paires cl-valeur reprsentant des enregistrements de donnes.
   * @param {StateValidator} [validator] - Une fonction de validation optionnelle appele lors de la modification d'un tat.
   */
  constructor(records?: RECORD, validator?: StateValidator) {
    super();

    if (validator) this._validator = validator;

    // Si des enregistrements sont fournis, les ajouter au _storage
    if (records) {
      Object.keys(records).forEach((key) => {
        this.set(key, createState(records[key]));
      });
    }
  }

  /**
   * Rcupre la valeur du stockage en fonction d'une cl spcifie.
   * Si un validateur est dfini, il valide la nouvelle valeur avant de l'appliquer.
   * @template T - Le type de la valeur de l'tat.
   * @param {StorageKeys<RECORD>} key - La cl utilise pour accder  un enregistrement spcifique en stockage.
   * @returns {State<T>["mutator"]} La fonction `mutator` associe  la cl spcifie de l'objet `State`.
   * @example
   * ```typescript
   * const storage = _storage.init({ count: 0 });
   * const [count, setCount] = storage.get("count");
   * setCount(10); // met  jour la valeur si la validation russit
   * ```
   */
  get<T = any>(key: StorageKeys<RECORD>): State<T>["mutator"] {
    if (!this._validator) return super.get(key)!;

    else {
      let [state, setter] = super.get(key)!;
      return [state, (newValue: T) => {
        let previousValue = state.value;
        let result = this._validator(key as string, previousValue, newValue);

        if (result instanceof Promise) {
          result
            .then(() => setter(newValue as any))
            .catch((error) => console.error(error));
        } else if (result == true) {
          setter(newValue as any);
        }
      }];
    }
  }
}

/** 
 * Type `Storage` qui tend partiellement `_storage`.
 * @template RECORD - Le type de l'enregistrement.
 */
export type Storage<RECORD extends Record<string, any>> = _storage<RECORD> & { [key in StorageKeys<RECORD>]: StorageValues<RECORD> };
