import { Notifier, Observable } from "@microsoft/fast-element";

/** 
 * La classe `State` gre et suit les valeurs d'tat avec des capacits d'historique et d'abonnement.
 */
export class State<T = any>{

  /** 
   * Proprit prive pour stocker l'historique des valeurs d'tat, limite aux 10 dernires entres.
  */
  private _history : T[] = [];

  /** 
   * Proprit prive pour stocker la valeur actuelle de l'tat, initialise  `null`.
  */
  private _value:T = null;

  /** 
   * Proprit prive pour notifier les observateurs des changements d'tat, initialise avec `Observable.getNotifier`.
  */
  private _notifier:Notifier = Observable.getNotifier( this );

  constructor( value = null ){
    this._value = value;
  }


  /** 
   * Getter pour la valeur actuelle de l'tat. Utilise `Observable.track` pour suivre les changements.
  */
  get value() {
    Observable.track(this, 'value');
    return this._value;
  };

  /**
   * Setter pour mettre  jour la valeur de l'tat. Ajoute la valeur actuelle  l'historique et notifie
   * les observateurs du changement.
   * @param {T} value - Nouvelle valeur de l'tat.
  */
  set value(value: T) {
    this._value = value;

    this._history = [ this._value , ...this._history.reverse() ].reduce(( results , value ) => {
      if(results.length > 10){
        results.push( value );
      };
      return results;
    } , []).reverse();

    Observable.notify(this, 'value');
  }

  /**
   * Mthode statique pour initialiser un nouvel objet `State` avec une valeur initiale facultative.
   * @param {T} [value=null] - Valeur initiale de l'tat.
   * @returns Une nouvelle instance de `State` avec la valeur spcifie.
  */
  static init<T = any>( value : T = null ){
    return new State<T>( value );
  }

  /**
   * Renvoie un tableau avec l'tat actuel et une fonction pour mettre  jour l'tat.
   * @returns Un tableau contenant l'tat actuel et une fonction de mise  jour.
  */
  get mutator():[ State<T> & T , ( newValue:T ) => void ]{
    return [ this as any , ( value:T ) => {
      this.value = value;
    } ]
  }

  /** 
   * Abonne une fonction de rappel qui sera appele  chaque changement de valeur de l'tat.
   * @param {function} callback - Fonction  appeler lors d'un changement de valeur.
  */
  subscribe( callback:(newValue: any) => void ){
    this._notifier.subscribe( { handleChange : ( state , key ) => {
      callback( state[key] );
    } } );
  };

  /**
   * Convertit l'tat en une valeur primitive pour les objets, ou en Proxy pour les objets complexes.
   * @returns La valeur primitive ou un Proxy pour les objets complexes.
  */
  [Symbol.toPrimitive](){
    return typeof this.value != "object" ? this.value : new Proxy( this.value , {
      get( target , key ){
        return target[key] ? target[key] : undefined;
      }
    } )
  }

}